import React, { useEffect, useRef, useState } from 'react';
import ReactDOM from 'react-dom';
import './popup.css';
import LocationCard from './LocationCard';
import { Box, Grid, IconButton, Paper, TextField } from '@material-ui/core';
import { AddCircle as AddIcon } from '@material-ui/icons';
import { setUserLocationsInStorage, getUserLocationsInStorage } from '../utils/storage';
const App = () => {
    const listingLocationRef = useRef('Auckland');
    const [userLocations, setUserLocations] = useState([]);
    const [locationInput, setLocationInput] = useState('');
    const [titleInput, setTitleInput] = useState('');
    useEffect(() => {
        getUserLocationsInStorage().then((userLocations) => setUserLocations(userLocations));
    }, []);
    const handleSubmit = (event) => {
        event.preventDefault();
        if (titleInput && locationInput) {
            setUserLocations((prevUserLocation) => [
                ...prevUserLocation,
                { locationTitle: titleInput, userLocation: locationInput }
            ]);
            setLocationInput((previousLocationInput) => (previousLocationInput = ''));
            setTitleInput((previousTitleInput) => (previousTitleInput = ''));
            setUserLocationsInStorage([
                ...userLocations,
                { locationTitle: titleInput, userLocation: locationInput }
            ]);
            console.log(userLocations);
        }
    };
    const handleLocationDeleteBtnClick = (index) => {
        userLocations.splice(index, 1);
        setUserLocations((prevUserlocations) => [...prevUserlocations]);
        setUserLocationsInStorage(userLocations);
    };
    return (React.createElement("div", null,
        React.createElement(Box, { mx: '4px', my: '8px' },
            React.createElement(Paper, null,
                React.createElement(Box, { px: '16px', py: '4px' },
                    React.createElement("form", { noValidate: true, autoComplete: 'off', onSubmit: handleSubmit },
                        React.createElement(Grid, { container: true, direction: 'row', justifyContent: 'center', alignItems: 'center' },
                            React.createElement(Grid, { item: true, xs: 3 },
                                React.createElement(TextField, { label: 'Title', variant: 'outlined', color: 'primary', value: titleInput, onChange: (event) => setTitleInput((input) => (input = event.target.value)) })),
                            React.createElement(Grid, { item: true, xs: 7 },
                                React.createElement(TextField, { label: 'Address', variant: 'outlined', color: 'primary', value: locationInput, onChange: (event) => setLocationInput((input) => (input = event.target.value)), onKeyUp: (event) => {
                                        if (event.key === 'Enter') {
                                            handleSubmit;
                                        }
                                    } })),
                            React.createElement(Grid, { item: true, xs: 2 },
                                React.createElement(IconButton, { type: 'submit' },
                                    React.createElement(AddIcon, { color: 'primary' })))))))),
        userLocations.map((location, index) => (React.createElement(LocationCard, { userLocation: location.userLocation, locationTitle: location.locationTitle, listingLocation: listingLocationRef.current, key: index, onDelete: () => handleLocationDeleteBtnClick(index), onEdit: () => { } }))),
        React.createElement(Box, { height: '8px' })));
};
const root = document.createElement('div');
document.body.appendChild(root);
ReactDOM.render(React.createElement(App, null), root);
